/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Fonts */
body {
  font-family: 'Arial', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #3c3c3c;
  background-color: #f5f5f5; /* light grey background for contrast */
}

/* Layout */
.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header */
header {
  background-color: #2980b9; /* blue header */
  color: #fff;
  padding: 1rem;
}

/* Main Content */
main {
  flex-grow: 1;
  padding: 2rem;
  background-color: #fff; /* white background for main content */
  border-radius: 5px; /* rounded corners */
  margin: 1rem; /* space around main content */
  box-shadow: 0 2px 5px rgba(0,0,0,0.1); /* subtle shadow for "lifted" effect */
}

/* Sections */
section {
  margin-bottom: 2rem;
}

/* Footer */
footer {
  background-color: #2980b9; /* same blue as header */
  color: #fff;
  padding: 1rem;
}

/* Utility classes */
.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Mobile */
@media (max-width: 768px) {
  .container {
    max-width: 100%;
  }
  main {
    margin: 0.5rem; /* smaller space around main content on small screens */
  }
}
