.resume {
  max-width: 800px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
  background-color: #fff; /* white background */
  color: #333; /* dark text for contrast */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* subtle shadow */
}

h1, h2, h3 {
  margin-bottom: 10px;
  color: #2980b9; /* blue headers */
}

h1 {
  font-size: 2.5em;
  margin-top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

h2 {
  margin-top: 30px;
}

ul {
  margin-left: 30px;
}

.highlights:hover,
.technical-skills:hover,
.education:hover,
.employment-experience:hover,
.experience:hover {
  background-color: #ecf0f1; /* light gray hover */
  padding: 10px;
  border-radius: 5px;
  transition: background-color 300ms, padding 300ms;
}

.fade-enter {
  opacity: 0;
  transform: translateY(-20px);
}

.fade-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 500ms, transform 500ms;
}

.fade-exit {
  opacity: 1;
  transform: translateY(0);
}

.fade-exit-active {
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 500ms, transform 500ms;
}

/* Mobile */
@media (max-width: 600px) {
  .resume {
    max-width: 100%;
    padding: 0 20px;
    justify-content: center;
  }

  .h1 {
    font-size: 1.5rem;
  }
}
