/* Home.css */

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f5f5f5; /* light grey background for contrast */
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  text-align: center;
  background-color: #fff; /* white background for main content */
  border-radius: 5px; /* rounded corners */
  margin: 1rem; /* space around main content */
  box-shadow: 0 2px 5px rgba(0,0,0,0.1); /* subtle shadow for "lifted" effect */
}

.hero-text {
  margin-bottom: 2rem;
  padding: 0 1rem;
  color: #3c3c3c; /* dark grey text for better readability */
}

.hero-text h1 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: #2980b9; /* blue headers for emphasis */
}

.hero-text p {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.hero-image {
  position: relative;
  width: 100%;
  max-width: 400px;
  overflow: hidden;
}

.hero-image img {
  width: 100%;
  height: auto;
}

/* CSSTransition styles */
.fade-appear,
.fade-enter {
  opacity: 0;
  transform: translateY(50px);
}

.fade-appear-active,
.fade-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 5000ms, transform 5000ms;
}

/* Media queries for responsiveness */
@media (min-width: 768px) {
  .hero {
    flex-direction: row;
    justify-content: space-between;
  }

  .hero-text {
    text-align: left;
  }

  .hero-text h1 {
    font-size: 3rem;
  }

  .hero-text p {
    font-size: 1.5rem;
  }

  .hero-image {
    max-width: 600px;
  }
}
