.footer {
  padding: 1.5rem 0;
  background-color: #2980b9; /* blue footer to match the header */
  color: #fff; /* white text for contrast */
  text-align: center;
}

.footer p {
  margin-bottom: 0;
}

.footer a {
  color: #fff; /* white links for contrast */
  text-decoration: none; /* remove underline */
}

.footer a:hover {
  color: #3498db; /* lighter blue for hover */
}
