/* contact.css */

.contact {
  padding: 3rem 0;
  background-color: #e8f0fe; /* light blue background */
}

.contact-text {
  text-align: center;
  margin-bottom: 2rem;
}

.contact-text h1 {
  font-size: 2rem;
  font-weight: bold;
  color: #185abc; /* dark blue headers */
}

.contact-form {
  max-width: 600px;
  margin: 0 auto;
}

.contact-form form {
  background-color: white;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}

.contact-form label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
  color: #185abc; /* dark blue labels */
}

.contact-form input[type="text"],
.contact-form textarea {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #185abc; /* dark blue border */
  border-radius: 0.25rem;
  margin-bottom: 1rem;
}

.contact-form textarea {
  min-height: 150px;
  resize: vertical;
}

.contact-form input[type="submit"] {
  background-color: #185abc; /* dark blue submit button */
  color: white;
  font-weight: bold;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.contact-form input[type="submit"]:hover {
  background-color: #0d3d8a; /* darker blue on hover */
}
