/* src/components/Header.css */
.header {
  padding: 1rem 0;
  background-color: #2980b9; /* blue header */
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.header-title {
  display: flex;
  margin: 0;
  color: #fff; /* white title */
  font-size: 1.5em; /* larger title */
}

.header-nav {
  display: flex;
  gap: 1rem;
}

.header-nav a {
  color: #fff; /* white nav links */
  text-decoration: none;
  padding: 0.5rem;
}

.header-nav a:hover,
.header-nav .active {
  background-color: #3498db; /* lighter blue for hover/active links */
  border-radius: 0.25rem;
}

/* Mobile */
@media (max-width: 768px) {
  /* header containter */
  .header-container {
    flex-direction: column;
    align-items: center;
  }
  
  .header-title {
    margin-bottom: 1rem;
    align-items: center;
  }
}
